import React, { useState } from 'react';
import HeaderContactComponent from './header-contact/header-contact.component';
import { Container, Modal, Nav, Navbar } from 'react-bootstrap';
import './header.component.css';

export default function HeaderComponent() {
    const [showMenu, setShowMenu] = useState(false);
    const [showContact, setShowContact] = useState(false);

    const handleCloseMenu = () => setShowMenu(false);
    const handleToggleMenu = () => setShowMenu(!showMenu);

    const handleCloseContact = () => setShowContact(false);
    const handleShowContact = () => setShowContact(true);

    return (
        <header className='w-full text-center'>
            <Navbar expand="lg" className="bg-light">
                <Container fluid>
                    <div className='d-lg-none'>
                        <Navbar.Brand href="#home" className="mt-4 me-lg-4">
                            <img
                                src="images/logo.svg"
                                width="150"
                                height="30"
                                className="d-inline-block align-top"
                                alt="Logo"
                            />
                        </Navbar.Brand>
                    </div>
                   <div className='d-none d-lg-block'>
                   <Navbar.Brand href="#home" style={{
                    marginLeft: '5vw',
                   }}>
                        <img
                            src="images/logo.svg"
                            width="150"
                            height="30"
                            className="d-inline-block align-top"
                            alt="Logo"
                        />
                    </Navbar.Brand>
                   </div>
                   
                   

                    {/** HEADER CONTACT DESKTOP */}
                    <div className='d-none d-lg-block'>
                        <div style={{
                            padding: '2vw'
                        }}>
                            <HeaderContactComponent />
                        </div>
                    </div>

                    {/** CUSTOM TOGGLE ICONS */}
                    <div className='d-flex justify-content-center align-items-center' style={{
                        padding: '2vw'
                    }}>
                        <div onClick={handleShowContact} className='me-3 d-lg-none'>
                            <img
                                src="images/icons/contacts.svg"
                                width={30}
                                height={30}
                                alt="Contact Icon"
                                className="nav-icon contact-icon"
                                style={{ cursor: 'pointer' }}
                            />
                        </div>
                        <div onClick={handleToggleMenu} className='d-lg-none'>
                            <img
                                src="images/icons/menu.svg"
                                width={30}
                                height={30}
                                alt="Menu Icon"
                                className={`nav-icon menu-toggle-icon ${showMenu ? 'rotate-open' : 'rotate-close'}`}
                                style={{ cursor: 'pointer' }}
                            />
                        </div>
                    </div>

                    <Navbar.Collapse in={showMenu} id="basic-navbar-nav">
    {/** MENU MOBILE */}
    <div className='d-lg-none'>
        <Nav className="menu-mobile text-center p-3">
            <Nav.Link 
                className='fs-4 d-flex justify-content-between align-items-center custom-nav-link'
                href="#vehicles"
            >
                <span className='text-start'>Motos</span>
                <img 
                    src="images/icons/arrow.svg" 
                    width={20} 
                    height={20} 
                    alt="Arrow Icon" 
                    className="nav-icon" 
                />
            </Nav.Link>
            <Nav.Link 
                className='fs-4 d-flex justify-content-between align-items-center custom-nav-link'
                href="#about"
            >
                <span className='text-start'>Sobre</span>
                <img 
                    src="images/icons/arrow.svg" 
                    width={20} 
                    height={20} 
                    alt="Arrow Icon" 
                    className="nav-icon" 
                />
            </Nav.Link>
            <Nav.Link 
                className='fs-4 d-flex justify-content-between align-items-center custom-nav-link'
                href="#vendor"
            >
                <span className='text-start'>Vendedor</span>
                <img 
                    src="images/icons/arrow.svg" 
                    width={20} 
                    height={20} 
                    alt="Arrow Icon" 
                    className="nav-icon" 
                />
            </Nav.Link>
        </Nav>
    </div>
</Navbar.Collapse>

                </Container>
            </Navbar>

            {/** MODAL HEADER CONTACT MOBILE */}
            <Modal show={showContact} onHide={handleCloseContact} backdrop={false}>
                <Modal.Header className='modal-header'>
                    <Modal.Title className='modal-title'>Nossos contatos</Modal.Title>
                    <button type="button" className='close-button' onClick={handleCloseContact}>
                        <img src="images/icons/close.svg" alt="Close" width={20} height={20} />
                    </button>
                </Modal.Header>

                <Modal.Body>
                    <HeaderContactComponent />
                </Modal.Body>
            </Modal>
        </header>
    );
}
