import React, { useEffect, useState } from "react";
import './vehicles.components.css';
import vehicleService from "../../services/vehicle.service";
import { Button, Card, Col, Container, Image, Row } from "react-bootstrap";
import VehicleDetailComponent from "./vehicle-detail/vehicle-detail.component";

interface Vehicle {
    id: number;
    name: string;
    image: string;
    description: string;
    valor: number;
    qtdParcelas: number;
}

export default function VehiclesComponent() {
    const [vehicles, setVehicles] = useState<any[]>([]);
    const [selectedVehicle, setSelectedVehicle] = useState<Vehicle | null>(null);
    const [showDetail, setShowDetail] = useState(false);

    const handleShowDetail = (vehicle: Vehicle) => {
        setSelectedVehicle(vehicle);
        setShowDetail(true);
    };

    const handleCloseDetail = () => {
        setShowDetail(false);
        setSelectedVehicle(null);
    };

    useEffect(() => {
        async function fetchVehicles() {
            try {
                const response: any = await vehicleService.listVehicles();
                if (Array.isArray(response.data.motos)) {
                    setVehicles(response.data.motos);
                } else {
                    console.error('Unexpected data format:', response.data);
                }
            } catch (error) {
                console.error('Failed to fetch vehicles', error);
            }
        }

        fetchVehicles();
    }, []);

    return (
        <Container className="my-4 mb-4">
            <Row>
                <div className="container">
                    <div
                        className="p-5 text-center rounded-3">
                        <Image className="bi mt-4 mb-3" src="images/sale/sale.png" roundedCircle width={"180px"} height={"220px"} />
                        <h1
                            className="text-body-emphasis">Jumbotron with icon</h1>
                        <p
                            className="col-lg-8 mx-auto fs-5 text-muted">
                            This is a custom jumbotron featuring an SVG image at the top, some longer text that wraps early thanks to a responsive <code>.col-*</code> className
                            className, and a customized call to action.
                        </p>
                    </div>
                </div>

            </Row >


            <Row xs={1} sm={2} md={3} lg={3} className="g-4">
                {vehicles.map(vehicle => (
                    <Col key={vehicle.id}>
                        <Card>
                            <Card.Img
                                variant="top"
                                src={`images/vehicles/${vehicle.defaultImg}`}
                                onError={(e) => e.currentTarget.src = 'images/vehicles/default.jpg'} // Fallback image
                            />
                            <Card.Body>
                                <Card.Title>{vehicle.name}</Card.Title>
                                <Card.Text>{vehicle.description}</Card.Text>
                                <Button variant="primary" onClick={() => handleShowDetail(vehicle)}>
                                    Mais informaes
                                </Button>
                            </Card.Body>
                        </Card>
                    </Col>
                ))}
            </Row>
            <Row xs={1} sm={2} md={3} lg={3} className="d-flex justify-content-center p-5">
                    <button
                        className="btn btn-outline-danger btn-lg rounded-pill"
                        type="button">
                        Veja mais motos
                    </button>
            </Row>


            {/* Vehicle Detail Modal */}
            <VehicleDetailComponent show={showDetail} handleClose={handleCloseDetail} vehicle={selectedVehicle} />
        </Container >
    );
}
