import React from 'react';
import './App.css';
import HeaderComponent from './components/header/header.component';
import CarrosselComponent from './components/carrossel/carrossel.component';
import VehiclesComponent from './components/vehicles/vehicles.component';
import FeatureComponent from './components/feature/feature.component';
import ContactComponent from './components/contact/contact.component';
import PreFooterComponent from './components/pre-footer/pre-footer.component';
import FooterComponent from './components/footer/footer.component';

export default function App() {

  const handleClick = () => {
    window.open('https://wa.me/5511999999999', '_blank'); // Substitua pelo seu nmero de WhatsApp
  };


  return (
    <div className="page" id='home'> 
      <HeaderComponent />
      <section>
        <CarrosselComponent />
      </section>
      <section id='vehicles'>
        <VehiclesComponent />
      </section>
      <section>
        <ContactComponent />
      </section>
      <section>
        <PreFooterComponent />
      </section>
      <FooterComponent />

      <div className="whatsapp-icon" onClick={handleClick}>
        <img src="images/icons/whatsapp.svg" alt="WhatsApp" />
      </div>
    </div>
  );
}