import React, { useState } from 'react';
import { Modal, Button, Row, Col } from 'react-bootstrap'; // Importa o componente de financiamento
import FinancingVehicleComponent from '../financing-vehicle/financing-vehicle.component';

interface VehicleDetailProps {
    show: boolean;
    handleClose: () => void;
    vehicle: any | null;
}

export default function VehicleDetailComponent({ show, handleClose, vehicle }: VehicleDetailProps) {

    const [showForm, setShowForm] = useState(false); // Estado para alternar a exibio do formulrio
    const [selectedColor, setSelectedColor] = useState(vehicle?.color || "black");

    const handleForm = () => setShowForm(!showForm); // Alterna entre o formulrio e os detalhes do veculo

    if (!vehicle) return null;

    const imageSrc = vehicle.images[selectedColor] || vehicle.images["black"];
    const colors = Object.keys(vehicle.images);

    return (
        <Modal show={show} onHide={handleClose} size="lg">
            <Modal.Header closeButton>
                <Modal.Title>{vehicle.name}</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                {!showForm ? ( // Exibe os detalhes do veculo ou o formulrio, baseado no estado `showForm`
                    <Row>
                         <Col md={6}>
                            <img src={`images/vehicles/${imageSrc}`} className="img-fluid" alt={vehicle.name} />
                            <Row className="mt-3">
                                <Col>
                                    <div className="d-flex">
                                        {colors.map((color: string, index: number) => (
                                            <div
                                                key={index}
                                                className="color-square"
                                                style={{ backgroundColor: color.toLowerCase(), width: '30px', height: '30px', margin: '2px', borderRadius: '4px', cursor: 'pointer' }}
                                                onClick={() => setSelectedColor(color)}
                                            />
                                        ))}
                                    </div>
                                </Col>
                            </Row>
                        </Col>
                        <Col md={6} className='mt-4'>
                            <div className="d-flex flex-column justify-content-between h-100">
                                <div>
                                    <p><strong style={{ color: "black" }}>Descrio:</strong> <span style={{ color: "gray", fontWeight: 500 }}>{vehicle.description}</span></p>
                                    <p><strong style={{ color: "black" }}>Valor:</strong> <span style={{ color: "gray", fontWeight: 500 }}>R${vehicle.valor.toFixed(2)}</span></p>
                                    <p><strong style={{ color: "black" }}>Parcelas:</strong> <span style={{ color: "gray", fontWeight: 500 }}>{vehicle.qtdParcelas}x</span></p>
                                    <p><strong style={{ color: "black" }}>Ano:</strong> <span style={{ color: "gray", fontWeight: 500 }}>{vehicle.year || "2023"}</span></p>
                                    <p><strong style={{ color: "black" }}>Cor:</strong> <span style={{ color: "gray", fontWeight: 500 }}>{vehicle.color || "Preto"}</span></p>
                                    <p><strong style={{ color: "black" }}>Quilometragem:</strong> <span style={{ color: "gray", fontWeight: 500 }}>{vehicle.km || "0 km"}</span></p>
                                </div>
                                <Button variant="success" className="mt-3" onClick={handleForm}>Faa j seu financiamento</Button>
                            </div>
                        </Col>
                    </Row>
                ) : (
                    <FinancingVehicleComponent vehicle={vehicle} /> // Exibe o componente de financiamento
                )}
            </Modal.Body>

            <Modal.Footer>
                {!showForm ? (
                    <div className="d-flex justify-content-between w-100">
                        <Button variant="outline-danger" onClick={handleClose}>
                            Fechar
                        </Button>
                    </div>
                ) : (
                    <div className="d-flex justify-content-between w-100">
                        <Button variant="outline-danger" onClick={handleForm}>Voltar</Button>
                        <Button variant="outline-secondary" onClick={handleClose}>
                            Fechar
                        </Button>
                    </div>
                )}
            </Modal.Footer>
        </Modal>
    );
}
