import { Col, Container, Row, Button } from "react-bootstrap";

export default function HeaderContactComponent() {
    return (
        <Container fluid>
            {/* Mobile View */}
            <div className="d-lg-none">
                {/* Contato */}
                <Row className="mt-3 mb-4 align-items-center">
                    <Col xs={10}>
                        <div className="text-dark fw-bold">+1 (123) 456-7890</div>
                        <div className="text-dark fw-bold">+1 (987) 654-3210</div>
                    </Col>
                    <Col xs={2} className="d-flex justify-content-end">
                        <img src="images/icons/phone.svg" alt="Phone Icon" width={24} height={24} />
                    </Col>
                </Row>
                {/* Endereo */}
                <Row className="mb-4 align-items-center">
                    <Col xs={10}>
                        <div className="text-dark fw-bold">123 Main St</div>
                        <div className="text-dark fw-bold">City, State, 12345</div>
                    </Col>
                    <Col xs={2} className="d-flex justify-content-end">
                        <img src="images/icons/location.svg" alt="Address Icon" width={24} height={24} />
                    </Col>
                </Row>
                {/* Boto */}
                <Row style={{ marginTop: '2vw' }}>
                    <Col xs={12}>
                        <Button variant="outline-danger" className="w-100">
                            Entre em contato
                        </Button>
                    </Col>
                </Row>
            </div>

            {/* Desktop View (lg) */}
            <div className="d-none d-lg-flex d-xl-none justify-content-between align-items-center w-100">
                <Row style={{ marginLeft: '5vw' }}>
                    {/* Phone */}
                    <Col xs={4} className="d-flex align-items-center justify-content-center">
                        <img src="images/icons/phone.svg" alt="Phone Icon" width={24} height={24} className="me-3" />
                        <div className="text-dark fw-bold">
                            <div>+1 (123) 456-7890</div>
                            <div>+1 (987) 654-3210</div>
                        </div>
                    </Col>
                    {/* Address */}
                    <Col xs={4} className="d-flex align-items-center justify-content-center">
                        <img src="images/icons/location.svg" alt="Address Icon" width={24} height={24} className="me-3" />
                        <div className="text-dark fw-bold">
                            <div>123 Main St</div>
                            <div>City, State, 12345</div>
                        </div>
                    </Col>
                    {/* Button */}
                    <Col xs={4} className="d-flex align-items-center justify-content-center">
                        <Button variant="outline-danger" style={{ width: '200px', height: '50px' }}>
                            Entre em contato
                        </Button>
                    </Col>
                </Row>
            </div>

            {/* XXL Desktop View */}
            <div className="d-none d-xl-flex justify-content-between align-items-center w-100">
                <Row style={{ marginLeft: '20vw' }}>
                    {/* Phone */}
                    <Col xs={4} className="d-flex align-items-center justify-content-center">
                        <img src="images/icons/phone.svg" alt="Phone Icon" width={24} height={24} className="me-3" />
                        <div className="text-dark fw-bold">
                            <div>+1 (123) 456-7890</div>
                            <div>+1 (987) 654-3210</div>
                        </div>
                    </Col>
                    {/* Address */}
                    <Col xs={4} className="d-flex align-items-center justify-content-center">
                        <img src="images/icons/location.svg" alt="Address Icon" width={24} height={24} className="me-3" />
                        <div className="text-dark fw-bold">
                            <div>123 Main St</div>
                            <div>City, State, 12345</div>
                        </div>
                    </Col>
                    {/* Button */}
                    <Col xs={4} className="d-flex align-items-center justify-content-center">
                        <Button variant="outline-danger" style={{ width: '250px', height: '60px' }}>
                            Entre em contato
                        </Button>
                    </Col>
                </Row>
            </div>
        </Container>
    );
}
