import React from 'react';
import './pre-footer.component.css'; // Certifique-se de adicionar este arquivo CSS com os estilos personalizados

export default function PreFooterComponent() {
  return (
    <div className="swiper-wrapper">
      <div
        className="swiper-slide"
        style={{
          background: "rgba(0, 0, 0, 0.5) url('images/pre-footer/capa-south-site.jpg') no-repeat center center",
          backgroundSize: 'cover',
          minHeight: '500px',
          position: 'relative',
        }}
      >
        <div className="container h-100 d-flex align-items-center justify-content-center">
          <div className="jumbotron-classNameic-content text-center text-white">
            <div className="wow-outer">
              <hr className="bar" />
              <h2 className="sub-title fs-1">DIVERSOS MODELOS</h2>
            </div>
            
            <h1 className="fs-2" style={{
              fontWeight: 200,
              marginTop: '2vw'
            }}>Vrias <br /> Possibilidades</h1>
            <hr className="bar" />
          </div>
        </div>
      </div>
    </div>
  );
}
