import React from 'react';
import './carrossel.component.css'
import { Carousel } from 'react-bootstrap';

export default function CarrosselComponent() {
  return (
    <Carousel>
      <Carousel.Item interval={1000}>
        <img src="images/carrossel/slide-carrossel-1.svg" className="d-block w-100" alt="..." />
      </Carousel.Item>
      <Carousel.Item interval={500}>
        <img src="images/carrossel/slide-carrossel-2.svg" className="d-block w-100" alt="..." />
      </Carousel.Item>
      <Carousel.Item>
        <img src="images/slider-minimal-slide-1-1920x968.jpg" className="d-block w-100" alt="..." />
      </Carousel.Item>
    </Carousel>
  )
}